/* ============ NAV_BAR ============ */

.navigation-wrapper {
    width: clamp(34px, 9.5vw, 44px);
    background-color: transparent;
    border-right: none;
    z-index: 0;
}


.right-side {
    right: 0;
}

.right-side .navigationbox {
    border-radius: 20px 0 0 20px;
}

.right-side .navigationbox::before {
    right: 0;
    border-radius: 20px 0 0 20px;
}


.left-side {
    left: 0;
}

.left-side .navigationbox {
    border-radius: 0 20px 20px 0;
}

.left-side .navigationbox::before {
    left: 0;
    border-radius: 0 20px 20px 0;
}


.navigationbox {
    position: relative;
    width: 100%;
    height: 100%;
    background: var(--color1-dr-th);
    display: grid;
    justify-items: center;
    align-items: center;
    align-content: stretch;
}

.navigationbox::before {
    content: '';
    position: absolute;
    width: calc(100% + 3px);
    height: calc(100% + 6px);
    background: var(--gradient-dr-th-90deg);
    top: -3px;
    z-index: -1;
    background-size: 200%;
    animation: ln-bg-gr-animation2 2s ease infinite;
}


.navigation-menu-tool {
    stroke: var(--text-dr-th);
    margin: clamp(4px, 1vh, 7px) 0;
    width: clamp(20px, 6vw, 25px);
    height: clamp(20px, 6vw, 25px);
}